const rulesList = [
    {
        number: 1,
        rule: "Izvairieties no tieiem saules stariem",
    },
    {
        number: 2,
        rule: "Uzglabjiet iepakojumu vs, tum viet.",
    },
    {
        number: 3,
        rule: "Uzglabanas temperatra nedrkst bt zemka par 0C un augstka par 20C",
    },
    {
        number: 4,
        rule: "Transportjot sulu, prliecinieties, ka sulas iepakojumam nepieskaras asi priekmeti",
    },
    {
        number: 5,
        rule: "Neatskrvjiet krnu un neaujiet gaisam iekt iepakojum",
    },
    {
        number: 6,
        rule: "Neizmantojiet iepakojumu atkrtoti",
    },
];

const Rules = () => {
    return (
        <section className="rules" id="rules">
            <div className="container">
                <h2 className="page__title">NOTEIKUMI</h2>
                <div className="rules__inner">
                    <div className="rules__inner-text">
                        <span>Sulu Fabrika</span> nedod garantiju derguma
                        termiam. Ja visi uzturanas noteikumi bija ievroti,
                        tad aizvrta veida sula nezauds savas pabas un
                        nebojsies gadu un vairk, atvrta veida sulu ieteicams
                        izdzert mnea laik. Galvenais faktors, kas ietekme
                        bolu sulas derguma termiu ir bolu stvoklis. boliem
                        jbt laicgi salastiem no koka, vai ar no zemes,
                        neaujot bojanas procesam skties. Nedrkst izmantot
                        sulas spieanai jau satrdjuos bolus, jo tie sasina
                        sulas derguma laiku un ts garu. <br />
                        <b>
                            Noteikumi zemk, esiet uzmangi un izbaudiet sulu!
                        </b>
                    </div>

                    <div className="rules__list-box">
                        <ul className="rules__list">
                            {rulesList.map((rule) => {
                                return (
                                    <li
                                        className="rules__item"
                                        key={rule.number}
                                    >
                                        <div className="rules__number">
                                            {rule.number}
                                        </div>

                                        <div className="rules__text">
                                            {rule.rule}
                                        </div>
                                    </li>
                                );
                            })}
                        </ul>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Rules;
